import React, { useState, useEffect } from "react";
import DataGrid, {
  Column,
  Selection,
  Editing,
  Scrolling,
  Paging,
  FilterRow,
} from "devextreme-react/data-grid";
import { summary } from "./shared";

export default function SubDataGrid(props) {
  let refGrid = React.useRef();
  const [dataSource, setDataSource] = useState([]);

  useEffect(() => {
    let isAdding = props.data.row.isNewRow;
    let source = props?.data.column.lookup.dataSource;
    if (isAdding) console.log(isAdding, "isAdding useEffect ::: ", source);
    // source.forEach((elem) => {
    //   // elem.cant = 0;
    // });
    // setDataSource(source);
    // }, [props?.data.column.lookup.dataSource]);
  }, []);

  function onRowUpdated() {
    let isAdding = props.data.row.isNewRow;
    let data = refGrid.current.instance.getSelectedRowsData();
    console.log(isAdding, "onRowUpdated ::: ", data);
    props.data.setValue(data);
  }

  // function onSelectionChanged(e) {
  //   e.component.refresh(true);
  // }

  return (
    <DataGrid
      ref={refGrid}
      dataSource={props?.data.column.lookup.dataSource}
      onRowUpdated={onRowUpdated}
      // onSelectionChanged={onSelectionChanged}
    >
      <Editing mode="cell" allowUpdating={true} />
      <Selection mode="multiple" />
      <Scrolling mode="virtual" />
      <Paging enabled={true} pageSize={10} />
      <FilterRow visible={true} />

      <Column dataField="nombre" allowEditing={false} />
      <Column dataField="tipo" allowEditing={false} />
      <Column dataField={"cant"} dataType={"number"} />
      <Column
        dataField="subtotal"
        alignment="right"
        format="currency"
        calculateCellValue={(rowData) => {
          let es_mayorista = rowData.es_mayorista === "Si";
          let precio =
            rowData[es_mayorista ? "precio_mayorista" : "precio_minorista"];
          rowData.subtotal = rowData.cant * precio;
          return rowData.cant * precio;
        }}
      />
      {summary(refGrid, true)}
    </DataGrid>
  );
}
