import React, { useState, useEffect } from "react";
import "./styles.scss";
import DataGrid, {
  Pager,
  Paging,
  FilterRow,
  HeaderFilter,
  Editing,
  Lookup,
  Column,
  Grouping,
  GroupPanel,
  RequiredRule,
  Export,
  ColumnChooser,
  Selection,
  Summary,
  TotalItem,
  MasterDetail,
} from "devextreme-react/data-grid";
import {
  getAll,
  getById,
  adding,
  updating,
  deleting,
} from "../../api/services";
import { useNavigation } from "../../contexts/navigation";
import { alert } from "devextreme/ui/dialog";
import LoadIndicator from "devextreme-react/load-indicator";
import { jsPDF } from "jspdf";
import { exportDataGrid } from "devextreme/pdf_exporter";
import { getUserRole } from "../../api/auth";
import SubDataGrid from "./subs-edit.js";
import { DetailTemplate } from "./subs-master-grid";

let userRole = getUserRole();
let onlyAdmin = userRole === 1;

const estadoPago = ["Pendiente", "Pagado", "Parcial"];
const tipoProducto = ["Retornable", "Descartable"];

export default function Generic() {
  let refGrid = React.useRef();
  const nav = useNavigation();
  const [DS, setDS] = useState([]);
  const [view, setView] = useState();
  const [columnsDS, setColumnsDS] = useState({});
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    let _view = nav.navigationData?.currentPath.replace("/", "");
    if (_view && window.location.href.includes(_view)) setView(_view);
  }, [useNavigation()]);

  useEffect(() => {
    setLoading(true);
    if (!view) return;
    getAll(view).then((response) => {
      if (!response?.length) return;
      let cols = {};
      let withOptions = Object.keys(response[0])?.filter((col) => {
        return col?.includes("_id");
      });
      let cant = withOptions.length;
      let count = 0;
      if (cant) {
        withOptions?.map((col) => {
          let table_name = col.replace("_id", "s");
          let field = col.replace("_id", "");
          getAll(table_name).then((newDS) => {
            if (!newDS?.length) return;
            newDS?.forEach((p) => (p.nombre = p.nombre?.trim()));
            cols[field] = newDS;
            response.map((x) => {
              let temp = newDS?.find((y) => y.id === x[col])?.nombre?.trim();
              if (temp) x[field] = temp;
            });
            count++;
            if (count === cant) {
              setColumnsDS(cols);
              setDS(response);
              setLoading(false);
            }
          });
        });
      } else {
        setDS(response);
        setLoading(false);
      }
    });
  }, [view]);

  function formatingDS_FromNameToID(data, keepID = false) {
    let newObj = {};
    let keys = Object.keys(DS[0]);

    keys.map((x) => {
      if (x === "id" && !keepID) return;
      let field = x.replace("_id", "");
      if (x.includes("_id")) {
        newObj[x] = columnsDS[field].find((y) => y.nombre === data[field])?.id;
      } else if (!Object.keys(columnsDS).find((y) => y === x)) {
        newObj[x] = data[field];
      }
    });

    if (view === "ventas") {
      newObj.id_productos = data.producto.map((p) => p.id).join();
      newObj.cant = data.producto.map((p) => p.cant).join();
    }

    return newObj;
  }

  function onRowInserting(e) {
    let data = e.data;
    let newObj = formatingDS_FromNameToID(data);

    console.log("newObj --------> ", newObj);

    // check if product is already added to stock puntos de venta
    if (!productoAlreadyExist(DS, newObj)) {
      adding(view, newObj).then((response) => {
        if (response.length) alert("Agregado con éxito", "Agregado");
        else {
          alert("Algo salió mal", "Error");
          return;
        }

        // update stock
        if (view === "movimientos_stock") {
          updatingStockPuntosVenta(newObj);
        }
      });
    } else {
      alert(
        "Este producto ya existe en el listdo de stocks de ese punto de venta, modifique el stock del producto desde el icono del lapiz a la derecha de la fila"
      ).then(() => {
        window.location.reload();
      });
    }
  }

  function onRowUpdated(e) {
    let data = e.data;
    let newObj = formatingDS_FromNameToID(data, true);
    updating(view, newObj).then((response) => {
      if (!response) alert("Actualizado con éxito", "Actualizado");
      else alert("Algo salió mal", "Error");
    });
  }

  function onRowRemoving(e) {
    let { id } = e.data;
    deleting(view, id).then((response) => {
      if (!response) alert("Eliminado con éxito", "Eliminado");
      else alert("Algo salió mal", "Error");
    });
  }

  function updatingStockPuntosVenta(newElem) {
    getAll("stocks_locales").then((response) => {
      let elemSumanding = response.find(
        (v) =>
          v.locale_id !== newElem.locale_id &&
          v.producto_id === newElem.producto_id
      );
      let elemRestnding = response.find(
        (v) =>
          v.locale_id === newElem.locale_id &&
          v.producto_id === newElem.producto_id
      );

      try {
        elemSumanding.cant += newElem?.cant;
        elemRestnding.cant -= newElem?.cant;
      } catch (err) {
        alert(
          "Por favor, chequear si ambos puntos de venta cuentan con ese producto agregado - Desde el menú 'Stocks Puntos de venta'"
        );
      }

      for (let index = 0; index < 2; index++) {
        updating("stocks_locales", index === 0 ? elemRestnding : elemSumanding);
      }
    });
  }

  function productoAlreadyExist(DS, newObj) {
    if (view === "stocks_locales") {
      let productoAlreadyExist = DS.filter(
        (p) =>
          p.producto_id === newObj.producto_id &&
          p.locale_id === newObj.locale_id
      );
      if (productoAlreadyExist.length) return true;
      else return false;
    }
  }

  function onEditorPrepared(e) {
    if (
      e.parentType === "dataRow" &&
      (e.name.includes("_id") || fieldsHiddenOnEditMode.includes(e.name))
      // ||  (view === "ventas" && e.name === "producto")
    ) {
      e.component.columnOption(e.dataField, "formItem.visible", false);
    } else return;
  }

  function calculateSelectedRow(options) {
    if (options.name === "SelectedRowsSummary") {
      if (options.summaryProcess === "start") {
        options.totalValue = 0;
      } else if (options.summaryProcess === "calculate") {
        options.totalValue += options.value.total;
        setTimeout(() => {
          // console.log(options, " refGrid :: ", options.totalValue);
          // !Number.isInteger(options.totalValue) &&
          //   refGrid.current.instance.refresh();
        }, 150);
      }
    }
  }

  const onExporting = (e) => {
    const doc = new jsPDF();
    exportDataGrid({
      jsPDFDocument: doc,
      component: e.component,
      indent: 5,
      landscape: false,
    }).then(() => {
      doc.save(view + ".pdf");
    });
  };

  const fieldsHiddenOnEditMode = [
    "fecha",
    "id",
    "total",
    "estado_pago",
    "tipo",
    "es_mayorista",
    "id_productos",
    "cant",
    "usuario",
    "total",
  ];
  const colsHiddenOnGrid = [
    "estado_pago",
    "comentario",
    "es_mayorista",
    "usuario",
    "producto", // need hide only on ventas
  ];
  const colsHiddenOnGrid_fromcolumnDS = [
    "estado_pago",
    "usuario",
    "es_mayorista",
    "producto", // need hide only on ventas
  ];
  const noRequiered = ["comentario", "tel"];
  const colsNumberDataType = [
    "tel",
    "precio_minorista",
    "precio_mayorista",
    "role",
    "total",
    "cant",
    "stock_mc3",
    "stock_jardin",
  ];
  const columnsPrecioFormat = ["precio_minorista", "precio_mayorista", "total"];

  if (DS.length)
    return (
      <React.Fragment>
        <div className={"content-block dx-card responsive-paddings"}>
          {loading && (
            <LoadIndicator width={"24px"} height={"24px"} visible={true} />
          )}
          <h6 className={"content-block"}>
            {view.replace("_", " ").toUpperCase()}
          </h6>

          <DataGrid
            keyExpr={"id"}
            ref={refGrid}
            className={"dx-card wide-card"}
            dataSource={DS}
            showBorders={true}
            focusedRowEnabled={true}
            defaultFocusedRowIndex={0}
            columnAutoWidth={true}
            columnHidingEnabled={true}
            allowColumnReordering={true}
            onExporting={onExporting}
            onRowInserting={onRowInserting}
            onRowUpdated={onRowUpdated}
            onRowRemoving={onRowRemoving}
            onEditorPreparing={onEditorPrepared}
          >
            <Paging defaultPageSize={10} />
            <Pager showPageSizeSelector={true} showInfo={true} />
            <HeaderFilter visible={true} />
            <Editing
              mode="form" // popup
              allowUpdating={onlyAdmin}
              allowAdding={true}
              allowDeleting={onlyAdmin}
            />
            <Grouping allowGrouping={true} contextMenuEnabled={true} />
            <GroupPanel visible={true} />
            <FilterRow visible={true} />
            <Export enabled={true} />
            <ColumnChooser enabled={true} />
            <Selection mode="single" />
            <Export
              enabled={true}
              formats={["pdf"]}
              allowExportSelectedData={true}
            />

            {Object.keys(columnsDS || {}).map((col) => {
              if (
                (columnsDS[col].length &&
                  !colsHiddenOnGrid_fromcolumnDS.includes(col)) ||
                (view === "ventas" && col !== "producto")
              )
                return (
                  <Column
                    dataField={col}
                    caption={
                      view === "movimientos_stock" && col === "locale"
                        ? "P. de venta Origen"
                        : col.replace("_", " ")
                    }
                    groupIndex={
                      view === "stocks_locales" && col === "locale" ? 0 : -1
                    }
                  >
                    <Lookup
                      dataSource={columnsDS[col]}
                      valueExpr="nombre"
                      displayExpr="nombre"
                      allowClearing={true}
                    />
                    <RequiredRule message="campo requerido" />
                  </Column>
                );
            })}

            {DS.length &&
              Object.keys(DS[0]).map((col, index) => {
                return (
                  !Object.keys(columnsDS).find((y) => y === col) && (
                    <Column
                      key={col + "_" + index}
                      dataField={col}
                      caption={col === "cant" ? "Cantidad" : col}
                      dataType={
                        colsNumberDataType.includes(col) ? "number" : "string"
                      }
                      visible={
                        !col.includes("id") && !colsHiddenOnGrid.includes(col)
                      }
                      allowExporting={!col.includes("id")}
                      showInColumnChooser={!col.includes("id")}
                      columnI
                      sortOrder={col === "fecha" ? "desc" : -1}
                      format={columnsPrecioFormat.includes(col) && "currency"}
                    >
                      {!noRequiered.includes(col) && (
                        <RequiredRule message="Campo requerido" />
                      )}
                    </Column>
                  )
                );
              })}

            {["productos"].includes(view) && (
              <Column dataField={"tipo"}>
                <Lookup dataSource={tipoProducto} />
                <RequiredRule message="Campo requerido" />
              </Column>
            )}

            {["ventas"].includes(view) && (
              <Column dataField={"estado_pago"}>
                <Lookup dataSource={estadoPago} />
                <RequiredRule message="Campo requerido" />
              </Column>
            )}

            {["ventas"].includes(view) && (
              <Column dataField={"es_mayorista"}>
                <Lookup dataSource={["Si", "No"]} />
                <RequiredRule message="Campo requerido" />
              </Column>
            )}

            {["ventas"].includes(view) && (
              <Column dataField="producto" editCellComponent={SubDataGrid}>
                <Lookup dataSource={columnsDS["producto"]} />
              </Column>
            )}

            {["ventas"].includes(view) && (
              <Column
                caption="Total"
                dataField="subtotal"
                alignment="right"
                format="currency"
                calculateCellValue={(rowData) => {
                  console.log(" rowData ::::: ", rowData);
                  if (
                    !Object.keys(rowData).length ||
                    !Number.isInteger(rowData.id) ||
                    rowData.id === 1
                  )
                    return;
                  let es_mayorista = rowData.es_mayorista === "Si";
                  let precio = 0;
                  let id_productos = rowData.id_productos.split(",");
                  let cant = rowData.cant?.split(",");

                  id_productos.map((id, index) => {
                    let temp = columnsDS["producto"].find(
                      (p) => p.id === parseInt(id_productos[index])
                    );
                    if (temp) {
                      precio +=
                        temp[
                          es_mayorista ? "precio_mayorista" : "precio_minorista"
                        ] * parseInt(cant[index]);
                    }
                  });

                  rowData.total = precio;
                  return precio;
                }}
              />
            )}

            {["ventas"].includes(view) && (
              <Summary calculateCustomSummary={calculateSelectedRow}>
                <TotalItem
                  name="SelectedRowsSummary"
                  summaryType="custom"
                  valueFormat="currency"
                  displayFormat="Sum: {0}"
                  showInColumn="subtotal"
                />
              </Summary>
            )}

            {["ventas"].includes(view) && (
              <MasterDetail
                enabled={true}
                component={(data) =>
                  DetailTemplate(data, columnsDS["producto"])
                }
              />
            )}
          </DataGrid>
        </div>
      </React.Fragment>
    );
}
