import {
  DataGrid,
  Column,
  Summary,
  TotalItem,
} from "devextreme-react/data-grid";

function calculateSelectedRow(options, refGrid, isFromEdit) {
  if (options.name === "SelectedRowsSummary") {
    if (options.summaryProcess === "start") {
      options.totalValue = 0;
    } else if (options.summaryProcess === "calculate") {
      if (isFromEdit) options.totalValue += options.value.subtotal || 0;
      else options.totalValue += options.value.subtotal;
      setTimeout(() => {
        !Number.isInteger(options.totalValue) &&
          refGrid.current.instance.refresh();
      }, 150);
    }
  }
}

export function summary(refGrid, isFromEdit = false) {
  return (
    <Summary
      calculateCustomSummary={(e) =>
        calculateSelectedRow(e, refGrid, isFromEdit)
      }
    >
      <TotalItem
        name="SelectedRowsSummary"
        summaryType="custom"
        valueFormat="currency"
        displayFormat="Sum: {0}"
        showInColumn="subtotal"
      />
    </Summary>
  );
}
