import React, { useRef, useEffect, useState } from "react";
import { DataGrid, Column } from "devextreme-react/data-grid";
import { summary } from "./shared";

export function DetailTemplate(props, productosDS) {
  let refGrid = useRef();
  const [data, setData] = useState([]);

  useEffect(() => {
    setTimeout(() => {
      let res = [];
      let id_productos = props.data.data.id_productos?.split(",");
      let cant = props.data.data.cant?.split(",");
      if (!id_productos) {
        // is adding
        id_productos = props.data.data.producto.map((x) => x.id);
        cant = props.data.data.producto.map((x) => x.cant);
      }
      id_productos.map((id, index) => {
        let temp = productosDS.find(
          (p) => p.id === parseInt(id_productos[index])
        );
        if (temp) {
          res.push({
            id: temp.id,
            cant: parseInt(cant[index]),
            nombre: temp.nombre,
            precio_mayorista: temp.precio_mayorista,
            precio_minorista: temp.precio_minorista,
          });
        }
      });

      setData(res);
      // refGrid.current.instance.refresh();
    }, 150);
  }, [props.data.data.producto]);

  let es_mayorista = props.data.data.es_mayorista === "Si";

  return (
    <React.Fragment>
      <DataGrid
        keyExpr={"id"}
        ref={refGrid}
        dataSource={data}
        showBorders={true}
        columnAutoWidth={true}
      >
        <Column dataField="nombre" caption="Producto" />
        <Column
          dataField={es_mayorista ? "precio_mayorista" : "precio_minorista"}
          format="currency"
        />
        <Column dataField="cant" />
        <Column
          dataField="subtotal"
          alignment="right"
          format="currency"
          allowEditing={false}
          calculateCellValue={(rowData) => {
            let precio =
              rowData[es_mayorista ? "precio_mayorista" : "precio_minorista"];
            rowData.subtotal = rowData.cant * precio;
            return rowData.cant * precio;
          }}
        />

        {summary(refGrid)}
      </DataGrid>
    </React.Fragment>
  );
}
